:root {
  --color-primero:#7d3c98;
  --color-primeroDark:#8e44ad;
  --color-segundo:#008080;
  --color-tercero:#ff0;
  --color-cuarto: #750D37;
  --blanco: #fffffe;
  --negro: #151516;
  --color-nota: #b8bfc6;
  --bg-nota: #363b40;
  --scrollOne:#48D1CC;
  --scrollTwo:#4682B4;
  --fuenteHeading: "Original Surfer", cursive;
  --fuenteText: "Handlee", cursive;
}

.dark {
  background-color: var(--negro);
}
.dark h1,
.dark h2,
.dark h3,
.dark h4,
.dark p,
.dark li,
.dark a {
  color: var(--blanco);
}
.dark .icons {
  filter: invert(100%);
}
.dark .barra {
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(20px);
  border-right: 0.1rem solid rgba(255, 255, 255, 0.5);
}
.dark .nav__link:hover {
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(20px);
  border: 0.1rem solid rgba(255, 255, 255, 0.5);
}
.dark .banner {
  background-image: url("/src/assets/img/sky_night.jpg");
}
.dark .box {
  background: linear-gradient(145deg, #222222, #292929);
  box-shadow: 20px 20px 60px #202020, -20px -20px 60px #2c2c2c;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: var(--fuenteText);
  transition: 0.5s;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

h1,
h2,
p,
ul {
  padding: 0;
  margin: 0;
}

.wrapper {
  max-width: 116rem;
  width: 60%;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .wrapper {
    width: 80%;
  }
}

.center {
  text-align: center;
}

.writings__title {
  margin: 4rem 0 0 0;
  font-size: 3rem;
}
@media screen and (min-width: 768px) {
  .writings__title {
    font-size: 3.8rem;
  }
}
.writings__subtitle {
  font-size: 2rem;
}
.writings__text {
  margin: 1.6rem 0;
}

.title {
  margin: 3rem 0;
  font-size: 2rem;
}
@media screen and (min-width: 768px) {
  .title {
    font-size: 3.8rem;
  }
}

/* =====BARRA LATERAL MENU===== */
.barra {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 5rem;
  height: 100vh;
  color: var(--negro);
  padding: 0.5rem;
  transition: 0.5s;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-right: 0.1rem solid rgba(0, 0, 0, 0.5);
}
@media screen and (min-width: 1024px) {
  .barra {
    width: 6rem;
  }
}

.barra__nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.barra__nav-brand {
  visibility: hidden;
}
@media screen and (min-width: 1024px) {
  .barra__nav-brand {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2rem;
    visibility: visible;
  }
}

.nav__toggle {
  width: 5rem;
  padding: 0.5rem;
  cursor: pointer;
}

.nav__logo {
  color: var(--negro);
  font-weight: 600;
  font-size: 1.2rem;
}

.nav__link {
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 2rem;
  padding: 1rem;
  color: var(--negro);
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  transition: 0.3s;
  cursor: pointer;
  position: relative;
  border: 0.1rem solid transparent;
}
.nav__link:hover {
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 0.1rem solid rgba(0, 0, 0, 0.5);
}

.nav__icon {
  width: 2rem;
}
@media screen and (min-width: 1024px) {
  .nav__icon {
    width: 3rem;
  }
}

.nav__name {
  font-size: 1.5rem;
}

.expander {
  width: 17rem;
}

/* =====HERO===== */
.hero {
  display: grid;
}
@media screen and (min-width: 768px) {
  .hero {
    grid-template-columns: 2fr 1fr;
  }
}

@media screen and (min-width: 1024px) {
  .hero__images img {
    height: 66rem;
    width: 100%;
    object-fit: cover;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 3rem;
  font-family: var(--fuenteHeading);
}
@media screen and (min-width: 768px) {
  .hero__text {
    padding-top: 0;
    row-gap: 2rem;
  }
}

@media screen and (min-width: 768px) {
  .hero__title {
    font-size: 4rem;
  }
}
@media screen and (min-width: 1024px) {
  .hero__title {
    font-size: 6rem;
  }
}

@media screen and (min-width: 768px) {
  .hero__subtitle {
    font-size: 2.2rem;
  }
}

.name {
  color: var(--color-cuarto);
}
@media screen and (min-width: 1024px) {
  .name {
    font-size: 2.5rem;
  }
}

/* =====BANNER===== */
.banner {
  width: 100%;
  text-align: center;
  background-image: url("/src/assets/img/sky.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 5rem 0;
  margin-bottom: 2rem;
  z-index: -1;
  overflow: hidden;
}

.banner__text {
  font-size: 4rem;
  margin: 0;
  position: relative;
  display: inline-block;
  animation: moveOne 15s infinite linear;
}
@media screen and (min-width: 376px) {
  .banner__text {
    animation: moveTwo 15s infinite linear;
  }
}
@media screen and (min-width: 768px) {
  .banner__text {
    animation: moveThree 15s infinite linear;
  }
}
@media screen and (min-width: 1024px) {
  .banner__text {
    animation: moveFour 15s infinite linear;
  }
}

@keyframes moveOne {
  from {
    left: 376px;
  }
  to {
    left: -4800px;
  }
}
@keyframes moveTwo {
  from {
    left: 768px;
  }
  to {
    left: -4800px;
  }
}
@keyframes moveThree {
  from {
    left: 1024px;
  }
  to {
    left: -4800px;
  }
}
@keyframes moveFour {
  from {
    left: 1280px;
  }
  to {
    left: -4800px;
  }
}
/* =====PAGINAS===== */
.box {
  padding: 1rem;
  height: 74rem;
  margin-bottom: 3rem;
  border-radius: 5rem;
  background: linear-gradient(145deg, #e3e3e3, #ffffff);
  box-shadow: 20px 20px 60px #d6d6d6, -20px -20px 60px #ffffff;
}
@media screen and (min-width: 768px) {
  .box {
    height: 62rem;
  }
}
@media screen and (min-width: 1024px) {
  .box {
    height: 50rem;
  }
}

.li__icon {
  opacity: 0;
  color: var(--color-primeroDark);
  width: 2rem;
}

.box__buttom ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 2rem;
  padding: 1rem;
}
@media screen and (min-width: 768px) {
  .box__buttom ul {
    flex-direction: row;
    justify-content: space-around;
  }
}
.box__buttom .li {
  cursor: pointer;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.5rem;
}
.box__buttom .li.activo {
  color: var(--color-primeroDark);
}
.box__buttom .li.activo .li__icon {
  opacity: 1;
}
.box__buttom .li:hover {
  color: var(--color-primeroDark);
  transition: color 0.5s ease;
}
.box__buttom .li .li a:visited {
  color: var(--color-primeroDark);
}

.box__content {
  position: relative;
}

.box__context {
  display: grid;
  grid-template-rows: min-content 1fr;
  padding: 2rem 1rem 0;
  position: absolute;
  opacity: 0;
  gap: 3rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .box__context {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: unset;
    align-items: center;
    justify-content: center;
  }
}
.box__context.activo {
  opacity: 1;
}

.box__img img {
  border-radius: 1rem;
}

.box__text p {
  padding-bottom: 1.5rem;
}
.box__text p:nth-child(1) {
  font-size: 3rem;
  font-family: var(--fuenteHeading);
}
@media screen and (min-width: 768px) {
  .box__text p:nth-child(1) {
    padding-bottom: 3rem;
  }
}